$vertical-padding: rem-calc(24);

.main-nav {
  @include xy-grid;
  @extend .align-middle;

  @include breakpoint(medium up) {
    padding-top: $vertical-padding / 2;
    padding-bottom: $vertical-padding / 2;
  }

  .hamburger {
    right: rem-calc(100);
    text-align: right;
    @include xy-cell(1);
  }

  &__logo {
    @include xy-cell(10);
    position: relative;
    z-index: 2000;

    .overlay-open & {
      filter: invert(0.5) brightness(150);
    }

    @include breakpoint(medium up) {
      @include xy-cell(3);
    }
    img {
      max-height: rem-calc(60);

      @include breakpoint(medium up) {
        max-height: rem-calc(80);
      }
    }
  }

  &__nav {
    text-align: right;
    @include xy-cell(8);

    ul {
      margin-left: 0;

      &:hover {
        a:not(:hover) {
          color: gray;
        }
      }
    }
    li {
      display: inline-block;
      list-style-type: none;
      margin-left: 0;
      padding-left: rem-calc(48);
      text-transform: uppercase;
      letter-spacing: rem-calc(1);

      @include breakpoint(medium only) {
        padding-left: rem-calc(24);
      }
    }

    a {
      padding-top: rem-calc(8);
      padding-bottom: rem-calc(8);
      display: inline-block;
      transition: all 0.3s ease-out;

      @include breakpoint(medium only) {
        font-size: 80%;
      }

      &:hover {
        transform: scale(1.1);
      }
    }
  }
}
