.designer-feature {
  border: 1px solid map-get($colors, gray-10);
  border-left: none;
  padding: 4%;
  text-align: center;
  transition: all 1s ease-out;
  user-select: none;
  opacity: 0.9999;

  &:last-of-type {
    border-right: 0;
  }

  &:hover {
    background-color: $black;
    color: $white;

    .designer-feature__image {
      transform: scale(1.1) translateZ(0);
    }

    img {
      transform: scale(1.5) translateZ(0);
    }
  }

  &__image {
    @extend %circular_mask;
    transition: all 3s ease-out;
    opacity: 0.9999;
  }

  img {
    transition: all 6s ease-out;
    transform-origin: top center;
  }
}

.designer-profile {
  background-color: map-get($map: $colors, $key: "gray-5");
  // color: $white;

  @include breakpoint(small only) {
    padding-left: 8%;
    padding-right: 8%;
  }
}

.designer-nav {
  width: 100%;
  border-top: rem-calc(1) solid map-get($map: $colors, $key: "gray-10");
  padding-top: rem-calc(8);
  margin-left: 0;
  text-align: center;
  white-space: nowrap;
  overflow-x: auto;

  li {
    list-style-type: none;
    margin-left: 0;
    display: inline-block;
  }

  &__item {
    @extend .text-spaced-2;
    padding-top: rem-calc(16);
    padding-bottom: rem-calc(16);
    padding-left: rem-calc(24);
    padding-right: rem-calc(24);
    text-transform: uppercase;
    font-size: 60%;
    line-height: 1;
    border-right: rem-calc(1) solid map-get($map: $colors, $key: "gray-10");

    @include breakpoint(medium up) {
      font-size: 80%;
      padding-left: rem-calc(32);
      padding-right: rem-calc(32);
    }

    &:last-of-type {
      border-right: none;
    }
  }
}

.designer-calendar {
  min-height: 80vh;
}

.designer-calendar-icon {
  font-size: rem-calc(60);
  margin-bottom: rem-calc(24);

  @include breakpoint(medium up) {
    text-align: center;
    font-size: rem-calc(120);
  }
}

.catalogue-thumbnail {
  padding: rem-calc(64);

  @include breakpoint(medium up) {
    padding: rem-calc(120);
  }
  img {
    max-height: rem-calc(300);
    transition: all 0.3s ease-out;
  }

  &:hover {
    img {
      transform: scale(1.2);
    }
  }
}

$margin: rem-calc(4);
$negative-margin: $margin * -1;

.designer-collection {
  display: flex;
  flex-wrap: wrap;
  margin-left: $negative-margin;
  margin-right: $negative-margin;
  &::after {
    content: "";
    flex-grow: 9999999999999;
  }

  &__item {
    margin: $margin;
    position: relative;
    height: auto;
    flex-grow: 1;
    overflow: hidden;

    &:hover {
      img {
        transform: scale(1.3);
      }
    }

    @include breakpoint(medium up) {
      height: rem-calc(480);
    }
  }

  img {
    max-width: 100%;
    min-width: 100%;
    object-fit: cover;
    object-position: center 30%;
    vertical-align: top;
    transition: transform 1s ease-out;

    @include breakpoint(medium up) {
      height: rem-calc(480);
    }
  }
}
