.fashion-week-banner {
  position: relative;
  height: 100vh;
  max-height: rem-calc(700);
  width: 100%;
  background-size: cover;
  background-position: 60% 20%;
  color: $white;
  text-align: right;

  &__title {
    font-size: 28vw;
    font-weight: 100;
    line-height: 0.75;

    @include breakpoint(medium up) {
      font-size: 16vw;
      letter-spacing: rem-calc(-2);
    }
  }

  &__date {
    font-weight: 700;
    line-height: 1;
    margin-top: rem-calc(-4);
    font-size: 6vw;

    @include breakpoint(medium up) {
      font-size: 2.5vw;
    }
  }

  &__content {
    position: absolute;
    top: 0;
    right: 0;
    margin-top: 2%;
    margin-right: 2%;
  }

  &__calendar {
    position: absolute;
    bottom: 10%;
    right: rem-calc(24);
  }
}
.home-blog-featured {
  margin-bottom: rem-calc(16);

  &__synopsis {
    font-size: 90%;
    line-height: 1.5;
    // font-weight: 100;
  }

  &__title {
    margin-bottom: 0;
    font-weight: 500;
  }

  &__content {
    width: 100%;
    padding-top: rem-calc(8);
  }

  &--full {
    position: relative;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    .home-blog-featured__content {
      background-image: linear-gradient(rgba($black, 0), $black);
      padding: $global-padding;
      width: 94.5%;
      position: absolute;
      bottom: 0;
      // left: 0;
      color: $white;

      @include breakpoint(medium up) {
        width: 92.5%;
      }
    }
  }
}

.fashion-focus-banner {
  height: 100vh;
  width: 100%;
  background-size: 400%;
  background-position: 90% 100%;
  background-color: $black;
  color: $white;
  position: relative;

  @include breakpoint(medium up) {
    background-size: cover;
    height: 100vh;
    background-position: 80% bottom;
  }

  @include breakpoint(large up) {
    background-position: right bottom;
  }

  &__content-wrapper {
    position: relative;
    height: 100%;
    align-content: flex-end;

    @include breakpoint(medium up) {
      align-content: center;
    }
  }

  &__content {
    background-color: $black;
    position: relative;
    bottom: 0;
    z-index: 200;
    height: auto;

    &:before {
      content: "";
      user-select: none;
      position: absolute;
      border: rem-calc(4) solid $black;
      width: 100%;
      height: 100%;
      top: rem-calc(16);
      left: rem-calc(16);
    }
  }
}
