%circular_mask,
.circular_mask {
  width: 100%;
  padding-top: 100%;
  position: relative;
  overflow: hidden;
  margin-top: rem-calc(24);
  margin-bottom: rem-calc(24);
  border-radius: 100%;
  opacity: 0.999;
  border: 1px solid transparent;
  transform: translateX(0);
  z-index: 0;
  will-change: transform;

  img {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    object-fit: cover;
    transition: all 2s ease-out;
    transform-origin: center top;
  }
}

a {
  .circular_mask:hover {
    img {
      transform: scale(1.4);
    }
  }
}

.upcoming-schedule {
  background-color: $black;
  color: $white;
  vertical-align: middle;
  line-height: 1;
  padding: rem-calc(16) !important;

  a {
    color: inherit;

    &:hover {
      color: gray;
    }
  }

  @include breakpoint(medium up) {
    padding: rem-calc(48) !important;
  }

  &__subheader {
    font-weight: 400;
    text-transform: uppercase;
    vertical-align: middle;
    border-left: 2px solid rgba($color: $white, $alpha: 0.3);
    line-height: 1;
    letter-spacing: rem-calc(4);

    small {
      font-size: 50%;
      line-height: 1;
      vertical-align: middle;
      padding-left: rem-calc(8);
      color: rgba($color: $white, $alpha: 0.6);
    }
  }

  &__nav {
    text-align: right;
    font-size: 50%;
    padding-top: rem-calc(12);

    &__left,
    &__right {
      padding: rem-calc(4);
      display: inline-block;
      transition: all 0.3s ease-out;

      &:hover {
        transform: scale(1.2);
      }
    }

    &__left {
      transform-origin: center right;
    }
    &__left {
      transform-origin: center right;
    }
  }

  &__rail {
    // $childElement: ".upcoming-schedule__item";
    overflow-x: scroll;
    white-space: nowrap;
    margin-left: 0;
    margin-top: rem-calc(48);
    margin-bottom: rem-calc(48);
    -webkit-overflow-scrolling: touch;
    user-select: none;
    -ms-overflow-style: none;
    scrollbar-width: none;

    &::-webkit-scrollbar {
      display: none;
    }

    &:hover {
      .upcoming-schedule__item:not(:hover) {
        opacity: 0.6;
        filter: grayscale(100%);
      }
    }
  }

  &__next {
    color: rgba($color: $white, $alpha: 0.5);
    letter-spacing: 2px;
    font-weight: 400;
    font-size: 60%;
  }

  &__item {
    display: inline-flex;
    border-right: 1px solid rgba($color: $white, $alpha: 0.1);
    flex-direction: column;
    align-items: center;
    width: 45%;
    transition: all 3s ease-out;

    &:last-of-type {
      border-right: none;
    }

    @include breakpoint(medium up) {
      width: 30%;
    }

    @include breakpoint(large up) {
      width: 21%;
    }

    &__image {
      @extend %circular_mask;
      width: 60%;
      padding-top: 60%;

      img {
        transition: transform 3s ease-out;
        transform-origin: top center;
      }

      &:hover {
        img {
          transform: scale(1.5);
        }
      }
    }

    &__title {
      font-weight: 600;
    }
  }

  &__date-time {
    line-height: 1.2;
    font-weight: 400;
    font-size: 80%;
    letter-spacing: rem-calc(2);
    position: relative;
    padding-left: rem-calc(4);

    &:before {
      font-family: "iconfonts";
      content: "\E001";
      position: absolute;
      left: rem-calc(-16);
    }
  }
}
