.fashion-focus__page-banner {
  position: relative;
  min-height: 50vh;
  background-size: cover;
  background-position: right 25%;
  color: $white;

  @include breakpoint(medium up) {
    min-height: 30vh;
  }

  &__content {
    position: absolute;
    bottom: rem-calc(24);

    @include breakpoint(medium up) {
      bottom: rem-calc(48);
    }
  }
}

.fashion-focus-designer-profile {
  text-align: left;

  &__wrapper {
    margin-top: rem-calc(24);
    position: relative;
    background-color: $white;
    padding-top: rem-calc(48);
    padding-bottom: rem-calc(48);
  }

  &__nav {
    padding-top: rem-calc(24);

    @include breakpoint(medium up) {
      text-align: right;
      padding-top: 0;
    }

    a {
      padding: rem-calc(16);
      border-right: rem-calc(1) solid map-get($colors, "gray-10");
      text-transform: uppercase;
      font-size: 70%;
      @extend .text-spaced-2;

      @include breakpoint(small only) {
        &:first-child {
          padding-left: 0;
        }
      }

      @include breakpoint(medium up) {
        padding: rem-calc(24);
      }

      &:last-of-type {
        padding-right: 0;
        border-right: 0;
      }

      &:hover {
        transform: scale(1.2);
      }
    }
  }
}

.fashion-focus__designer-rail {
  overflow-x: scroll;
  white-space: nowrap;
  margin-left: 0;
  -webkit-overflow-scrolling: touch;
  user-select: none;
  -ms-overflow-style: none;
  scrollbar-width: none;

  &__item {
    display: inline-flex;
    border-right: 1px solid rgba($color: $white, $alpha: 0.1);
    flex-direction: column;
    align-items: center;
    width: 40%;
    transition: all 3s ease-out;
    padding-left: rem-calc(16);
    padding-right: rem-calc(16);

    @include breakpoint(medium up) {
      width: 22%;
      padding-left: rem-calc(24);
      padding-right: rem-calc(24);
    }

    @include breakpoint(large up) {
      width: 16%;
      padding-left: rem-calc(48);
      padding-right: rem-calc(48);
    }
  }
}

.alumni {
  &__heading {
    border-top: rem-calc(1) solid map-get($map: $colors, $key: "gray-10");
    padding-top: rem-calc(24);
  }

  @at-root &__rail {
    // margin-left: -$global-padding * 2;
    // margin-right: -$global-padding;
    // visibility: hidden;
    // height: 0;
  }

  &__class {
    position: relative;
    text-align: center;
    align-content: flex-end;
    align-items: flex-end;
    padding-bottom: rem-calc(16);

    &:before,
    &:after {
      content: "";
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      background-color: map-get($map: $colors, $key: "gray-10");
      bottom: rem-calc(0);
    }

    &:before {
      width: rem-calc(10);
      height: rem-calc(10);
      border-radius: 50%;
    }

    &:after {
      height: rem-calc(48);
      width: rem-calc(2);
      bottom: rem-calc(-48);
    }
  }
}
.focus-designer {
  position: relative;
  padding-top: rem-calc(100);
  padding-bottom: rem-calc(100);

  &::before {
    content: "";
    position: absolute;
    background-size: 300px 300px;
    opacity: 0.2;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url("/assets/img/textured-background.jpg");
  }

  &__designer {
    padding: rem-calc(24);
    padding-bottom: 0;

    @include breakpoint(medium up) {
      padding: rem-calc(48);
      padding-bottom: 0;
    }

    @include breakpoint(large up) {
      padding: rem-calc(80);
      padding-bottom: 0;
    }
  }
}
