.button {
  background-color: $black;
  color: $white;
  position: relative;
  z-index: 300;
  min-width: rem-calc(240);
  max-width: 100% !important;
  text-transform: uppercase;
  letter-spacing: rem-calc(2);
  text-align: left;
  transition: transform 0.3s ease-out;
  font-weight: 500;

  &.large {
    font-size: 110%;
  }

  &:hover {
    transform: scale(1.1);

    &:after {
      transform: scale(0.8);
    }
  }

  &:active {
    transform: scale(1.1) translateY(rem-calc(4));
  }

  &:before {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    border: rem-calc(2) solid $black;
    right: rem-calc(-6);
    bottom: rem-calc(-6);
  }

  &:after {
    content: icon-char(right-arrow);
    @include icon-styles;
    transform: scale(0.5);
    float: right;
    margin-right: rem-calc(-4);
    transition: transform 0.3s ease-out;
    transform-origin: right center;
  }

  &.alt {
    background-color: $white;
    color: $black;

    &:before {
      border-color: $white;
    }
  }
}
