.collection-filter {
  user-select: none;

  &__title {
    padding-bottom: rem-calc(8);
    border-bottom: 1px solid map-get($colors, gray-10);
  }

  &__bar {
    padding-top: rem-calc(8);
    padding-bottom: rem-calc(8);
  }

  &__bar__nav,
  &__bar__search {
    padding-top: rem-calc(8);
    padding-bottom: rem-calc(8);
  }

  &__bar__nav {
    overflow-x: scroll;
    white-space: nowrap;

    ul,
    li {
      margin-bottom: 0;
      margin-left: 0;
    }
    li {
      display: inline;
      list-style-type: none;
      padding-right: rem-calc(2);
    }
  }

  &__bar__search {
    border-top: 1px solid map-get($colors, gray-10);
    @include breakpoint(medium up) {
      border-left: 1px solid map-get($colors, gray-10);
      padding-left: rem-calc(8);
      border-top: 0;
    }

    &__icon {
      position: relative;
      top: rem-calc(5);
      font-size: 120%;
      color: map-get($colors, gray-50);
    }

    * {
      display: inline-block;
      width: auto !important;
    }
  }

  input.designer-search {
    border: none;
    margin-bottom: 0;
    box-shadow: none;
    display: inline-block;

    &::placeholder {
      letter-spacing: rem-calc(2);
      text-transform: uppercase;
      font-size: 70%;
      color: map-get($colors, gray-50);
    }
  }
}
