@for $num from 1 to 4 {
  .text-spaced-#{$num} {
    letter-spacing: rem-calc($num);
  }
}

small {
  line-height: rem-calc(12);
}

.text-bold {
  font-weight: 500;
}

.headline1 {
  font-size: rem-calc(48);
  line-height: 1;
  font-weight: 500;
}

@each $num in (20, 30, 40, 50, 60, 70, 80, 90, 100) {
  .f-#{$num} {
    font-size: percentage($num / 100);
    line-height: 1;
  }
}
