.video-thumbnail {
  position: relative;
  display: inline-block;

  &:after {
    content: icon-char(play);
    color: $white;
    z-index: 100;
    @extend %icon;
    position: absolute;
    left: 50%;
    top: 50%;
    font-size: 200%;
    line-height: 1;
    transform: translateY(-50%) translateX(-50%);
  }
}

.responsive-video {
  width: 100%;
  padding-bottom: 60%;
  position: relative;

  iframe {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }
}
