.collection-nav {
  text-align: center;
  padding-top: rem-calc(16);
  padding-bottom: rem-calc(16);
  border-bottom: 1px solid map-get($colors, gray-10);
  margin-bottom: rem-calc(16);
  user-select: none;

  * {
    margin: 0;
  }

  &__main {
    font-size: 120%;
    overflow-x: scroll;
    white-space: nowrap;

    &:hover {
      li:not(:hover) {
        opacity: 0.6;
      }
    }

    li {
      list-style-type: none;
      display: inline-block;
      padding: rem-calc(4);
      transition: all 1s ease-out;
      cursor: pointer;

      @include breakpoint(medium up) {
        padding-left: rem-calc(8);
        padding-right: rem-calc(8);
      }

      &:hover {
        transform: scale(1.2);
      }
    }
  }

  &__prev,
  &__next {
    font-size: 70%;
    letter-spacing: rem-calc(2);
    text-transform: uppercase;
    vertical-align: middle;
    cursor: pointer;
    transition: transform 0.3s ease-out;

    [class^="icon"] {
      font-size: 40%;
    }

    &:hover {
      transform: scale(1.1);
    }
  }
}
