$gray: rgba(205, 205, 205, 0.3);
.calendar {
  &__info {
    line-height: 0.8;
    opacity: 0.5;
  }
  &__header {
    &__date {
      font-size: clamp(7em, 15vw, 10em);
      line-height: 1;
      font-weight: 500;
      // border-right: rem-calc(1) solid gray;
    }

    &__month {
      font-weight: 600;
      margin-top: -16%;
      font-size: clamp(1.5em, 2.5vw, 4em);
    }
  }

  &__body {
    background-color: $black;
    color: $white;
  }

  &__event-wrapper {
    color: $white;
  }

  &__events-wrapper {
    display: flex;
    flex-basis: fit-content;
    flex-wrap: wrap;
    height: auto;
    align-items: stretch;
    flex-wrap: wrap;
    align-items: flex-start;

    a {
      color: $white;
    }
  }

  &__event {
    display: flex;
    padding: rem-calc(8);
    flex-grow: 1;
    border-right: rem-calc(1) solid $gray;
    border-bottom: rem-calc(1) solid $gray;
    align-items: center;
    height: rem-calc(100);
    justify-content: space-around;

    @include breakpoint(medium up) {
      align-items: flex-start;
      padding: rem-calc(24);
      height: rem-calc(120);
    }

    &:hover {
      &__image {
        filter: grayscale(0);
      }
    }

    &__image {
      width: rem-calc(60);
      padding-top: rem-calc(60);
      margin-top: 0;
      margin-bottom: 0;
      margin-right: rem-calc(24);
      filter: grayscale(100);
    }

    &__date {
      text-transform: uppercase;
      opacity: 0.5;
      font-size: 70%;
      letter-spacing: rem-calc(1);
    }
  }

  &__date-wrapper {
    padding: rem-calc(40);
    border-right: rem-calc(1) solid $gray;
    border-bottom: rem-calc(1) solid $gray;
  }
}
