.dropdown-menu {
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 1000;
  background-color: $black;
  box-shadow: rem-calc(8) rem-calc(16) rem-calc(100) rgba($black, 0.2);
  font-weight: 500;
  line-height: 1.4;
  overflow: hidden;
  height: 0;
  letter-spacing: rem-calc(-1);

  @include breakpoint(medium up) {
    text-align: right;
  }

  a {
    color: $white;
  }

  li > a {
    font-size: 180%;
    color: $white;

    @include breakpoint(medium up) {
      font-size: 200%;
    }
  }

  &__wrapper {
    width: 100%;
    min-height: 100vh;
    padding-top: rem-calc(120);
    padding-left: rem-calc(24);
    padding-bottom: rem-calc(100);

    @include breakpoint(medium up) {
      padding-top: rem-calc(100);
      padding-right: rem-calc(60);
      padding-left: 0;
    }
  }

  &__section-heading {
    font-size: 60%;
    text-transform: uppercase;
    color: rgba($black, 0.5);
  }

  ul,
  li {
    margin: 0;
    position: relative;
  }

  li {
    list-style-type: none;
  }

  .social-icons {
    a {
      &:last-of-type {
        padding-right: 0;
        margin-right: 0;
      }
    }
    .overlay-open & {
      a {
        border-right-color: rgba($white, 0.2);
      }
    }
  }
}
