.designer-calendar-icon {
  font-size: 3.75rem;
  margin-bottom: 1.5rem;
}

@media print, screen and (min-width: 40em) {
  .designer-calendar-icon {
    text-align: center;
    font-size: 7.5rem;
  }
}

.catalogue-thumbnail {
  padding: 4rem;
}

@media print, screen and (min-width: 40em) {
  .catalogue-thumbnail {
    padding: 7.5rem;
  }
}

.catalogue-thumbnail img {
  max-height: 18.75rem;
  transition: all 0.3s ease-out;
}

.catalogue-thumbnail:hover img {
  transform: scale(1.2);
}

.designer-collection {
  display: flex;
  flex-wrap: wrap;
  margin-left: -0.25rem;
  margin-right: -0.25rem;
}

.designer-collection::after {
  content: "";
  flex-grow: 9999999999999;
}

.designer-collection__item {
  margin: 0.25rem;
  position: relative;
  height: auto;
  flex-grow: 1;
  overflow: hidden;
}

.designer-collection__item:hover img {
  transform: scale(1.3);
}

@media print, screen and (min-width: 40em) {
  .designer-collection__item {
    height: 30rem;
  }
}

.designer-collection img {
  max-width: 100%;
  min-width: 100%;
  object-fit: cover;
  object-position: center 30%;
  vertical-align: top;
  transition: transform 1s ease-out;
}

@media print, screen and (min-width: 40em) {
  .designer-collection img {
    height: 30rem;
  }
}

.fashion-focus__page-banner {
  position: relative;
  min-height: 50vh;
  background-size: cover;
  background-position: right 25%;
  color: #fefefe;
}

@media print, screen and (min-width: 40em) {
  .fashion-focus__page-banner {
    min-height: 30vh;
  }
}

.fashion-focus__page-banner__content {
  position: absolute;
  bottom: 1.5rem;
}

@media print, screen and (min-width: 40em) {
  .fashion-focus__page-banner__content {
    bottom: 3rem;
  }
}

.fashion-focus-designer-profile {
  text-align: left;
}

.fashion-focus-designer-profile__wrapper {
  margin-top: 1.5rem;
  position: relative;
  background-color: #fefefe;
  padding-top: 3rem;
  padding-bottom: 3rem;
}

.fashion-focus-designer-profile__nav {
  padding-top: 1.5rem;
}

@media print, screen and (min-width: 40em) {
  .fashion-focus-designer-profile__nav {
    text-align: right;
    padding-top: 0;
  }
}

.fashion-focus-designer-profile__nav a {
  padding: 1rem;
  border-right: 0.0625rem solid rgba(0, 0, 0, 0.1);
  text-transform: uppercase;
  font-size: 70%;
}

@media print, screen and (max-width: 39.99875em) {
  .fashion-focus-designer-profile__nav a:first-child {
    padding-left: 0;
  }
}

@media print, screen and (min-width: 40em) {
  .fashion-focus-designer-profile__nav a {
    padding: 1.5rem;
  }
}

.fashion-focus-designer-profile__nav a:last-of-type {
  padding-right: 0;
  border-right: 0;
}

.fashion-focus-designer-profile__nav a:hover {
  transform: scale(1.2);
}

.fashion-focus__designer-rail {
  overflow-x: scroll;
  white-space: nowrap;
  margin-left: 0;
  -webkit-overflow-scrolling: touch;
  user-select: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.fashion-focus__designer-rail__item {
  display: inline-flex;
  border-right: 1px solid rgba(254, 254, 254, 0.1);
  flex-direction: column;
  align-items: center;
  width: 40%;
  transition: all 3s ease-out;
  padding-left: 1rem;
  padding-right: 1rem;
}

@media print, screen and (min-width: 40em) {
  .fashion-focus__designer-rail__item {
    width: 22%;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
  }
}

@media print, screen and (min-width: 64em) {
  .fashion-focus__designer-rail__item {
    width: 16%;
    padding-left: 3rem;
    padding-right: 3rem;
  }
}

.alumni__heading {
  border-top: 0.0625rem solid rgba(0, 0, 0, 0.1);
  padding-top: 1.5rem;
}

.alumni__class {
  position: relative;
  text-align: center;
  align-content: flex-end;
  align-items: flex-end;
  padding-bottom: 1rem;
}

.alumni__class:before, .alumni__class:after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(0, 0, 0, 0.1);
  bottom: 0;
}

.alumni__class:before {
  width: 0.625rem;
  height: 0.625rem;
  border-radius: 50%;
}

.alumni__class:after {
  height: 3rem;
  width: 0.125rem;
  bottom: -3rem;
}

.focus-designer {
  position: relative;
  padding-top: 6.25rem;
  padding-bottom: 6.25rem;
}

.focus-designer::before {
  content: "";
  position: absolute;
  background-size: 300px 300px;
  opacity: 0.2;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("/assets/img/textured-background.jpg");
}

.focus-designer__designer {
  padding: 1.5rem;
  padding-bottom: 0;
}

@media print, screen and (min-width: 40em) {
  .focus-designer__designer {
    padding: 3rem;
    padding-bottom: 0;
  }
}

@media print, screen and (min-width: 64em) {
  .focus-designer__designer {
    padding: 5rem;
    padding-bottom: 0;
  }
}

html,
body {
  width: 100%;
  overflow-x: hidden;
}
